import React from 'react';
import axios from 'axios';
import cookie from 'react-cookie';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';



class MainContent extends React.Component {
  constructor(props){
    super(props);

    this.token = "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..1fnUMb27ilrQFNt9Uyj1SW2P7bp2lyDfXWn_6D-tGs4";
    this.state = {

    }
  }

// NOTE:
/*
We have added my personal token below for demonstration purposes, otherwise
we would use react-cookie to access the user's token with: cookie.load('connect.sid');
*/
  componentDidMount() {
    if (this.token == "") {
      this.token = cookie.load('connect.sid');
    }


    axios({
      method: "post",
      url: 'https://publist.ai/api/v2/jobs.frontend',
      headers: {
          Authorization : `Bearer-${this.token}`,
          "Content-type" : "application/json"
        },
      data: {
          "query": "hi"
        },
      }
    ).then((response) => {
      this.setState({
        data: response.data.data
      })
    });
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    console.log(this.state);
  }

  render () {

    return(
      <div className="main-container">
        <style jsx>{`
          .main-container {
            width: 100%;
            max-width: 1600px;
            min-height: 100vh;
            padding: 50px 72px 0;
            background: #F6F9FC;
            z-index: 10;
          }
          .page-title {
            color: var(--color-text-dark);
            font-size: 28px;
            font-weight: 600;
          }
          .flex-center {
            display: flex;
            justify-content: center;
          }
          .flex-one {
            flex: 1;
          }
          .dash-title {
            color: #000;
            font-size: 18px;
            font-weight: 500;
            line-height: 18px;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
          }
          `}
        </style>
        <div className="flex-center">
          <div className="page-title">Search</div>
          <div className="flex-one"></div>
          <div className="dash-title">
            <div>Current User</div>
            <FontAwesomeIcon prefix="fa" icon="google" />
          </div>
        </div>
      </div>
    )
  }
}

export default MainContent;



